import React from 'react';
import Link from 'gatsby-link';
import Section from '../components/section';
import alex from '../../assets/images/alex.jpg';

import instagram from '../../assets/images/instagram.svg';
import linkedin from '../../assets/images/linkedin.svg';
import github from '../../assets/images/github.svg';

const Header = () => (
  <div className='d-flex justify-content-end container nav'>
    <a className='p-3 text-grey' target="_blank" href="mailto:alexbouchardd@gmail.com">
      alexbouchardd@gmail.com
    </a>
    <a className='p-3' target="_blank" href="https://github.com/alexbouchardd">
      <img src={github}/>
    </a>
    <a className='p-3' target="_blank" href="https://linkedin.com/in/alex-bouchard">
      <img src={linkedin}/>
    </a>
    <a className='p-3' target="_blank" href="https://www.instagram.com/alexbouchardd">
      <img src={instagram}/>
    </a>
  </div>
);

const IndexPage = ({ data }) => {
  const { edges: posts } = data.allMarkdownRemark;
  return (
    <div>
      <Header/>
      <div className='mb-6'>
        <div className='container intro text-center'>
          <img src={alex} className='mb-6 profile-picture'/>
          <p className='title'>Hey, I'm Alex.</p>
          <p className='text-muted'>I imagine, create and build digital products and experiences.</p>
        </div>
        {posts
          .map(({ node: post }) => {
            return (
              <Section key={post.id} post={post}/>
            );
          })}
      </div>
    </div>
  );
};


export const pageQuery = graphql`
  query IndexQuery {
    allMarkdownRemark(
      sort: { order: DESC, fields: [frontmatter___date] }
      limit: 1000
    ) {
      edges {
        node {
          html
          id
          frontmatter {
            date
            path
            title
            timeframe
            images {
              first
              second
            }
            gradient {
              left
              right
            }
            snippet
          }
        }
      }
    }
  }
`;

export default IndexPage



